@charset "UTF-8";
/* 公用样式 */
/*引入配置*/
/*设置主题颜色函数*/
/* 全局重置scss */
* {
  box-sizing: border-box; }

a {
  cursor: pointer;
  text-decoration: none; }

html, body {
  width: 100%;
  height: 100%;
  font-family: "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif; }

html {
  height: 100vh; }

body {
  font-size: 14px;
  margin: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  content: normal !important;
  border-style: none;
  pointer-events: none; }

a {
  text-decoration: none;
  color: #409eff;
  background-color: transparent; }

i,
em {
  font-style: normal; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

h1, h2, h3, h4, h5, h6, p, figure, form, blockquote {
  margin: 0; }

ul, ol, dl, dd {
  margin: 0;
  padding: 0; }

ul, ol {
  list-style: none outside none; }

h1, h2, h3 {
  line-height: 2;
  font-weight: normal; }

input {
  outline: none; }

textarea {
  outline: none;
  resize: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

input[type="number"] {
  -moz-appearance: textfield; }

/deep/ input::-webkit-outer-spin-button,
/deep/ input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

/deep/ input[type="number"] {
  -moz-appearance: textfield; }

#app {
  height: 100%;
  position: relative; }

[v-cloak] {
  display: none; }

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.ellipsis2,
.ellipsis3 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical; }

.ellipsis2 {
  -webkit-line-clamp: 2; }

.ellipsis3 {
  -webkit-line-clamp: 3; }

.text-left {
  text-align: left; }

.text-center {
  text-align: center;
  justify-content: center !important; }

.text-right {
  text-align: right; }

.color-primary {
  color: #409eff; }

.color-warning {
  color: #f4a028; }

.color-danger {
  color: #d13a38; }

.lf {
  float: left; }

.rt {
  float: right; }

.flex {
  display: flex; }

.inline-block {
  display: inline-block; }

.block {
  display: block; }

.container {
  width: 1200px;
  margin: 0 auto;
  position: relative; }

.height-100 {
  height: 100%; }

.width-100 {
  width: 100%; }

/* 全局重置 elementUI scss */
.el-table td {
  padding: 4px; }

.el-table th {
  padding: 8px 0;
  background: #f5f6fa; }

.el-pagination el-pager li:not(.disabled).active {
  border: none; }

.el-pagination button, .el-pagination span:not([class*=suffix]) {
  height: 32px;
  line-height: 32px; }

.el-pagination .el-pager > li {
  height: 32px;
  line-height: 32px; }

.el-pagination.is-background .btn-next,
.el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  border-radius: 0;
  background: #fff;
  border: 1px solid #eee;
  min-width: 32px; }

.el-pagination.is-background el-pager li:not(.disabled).active {
  border: none; }

.el-dialog {
  border-radius: 10px; }
  .el-dialog .el-dialog__header {
    background: #409eff;
    padding: 15px 20px;
    border-radius: 10px 10px 0 0;
    font-size: 16px;
    color: #ffffff; }
  .el-dialog .el-dialog__title {
    color: #ffffff; }
  .el-dialog .el-dialog__headerbtn {
    top: 16px; }
  .el-dialog .el-dialog__headerbtn .el-dialog__close {
    color: #ffffff; }

.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item {
  margin-bottom: 30px; }

.el-input__prefix, .el-input__suffix {
  color: #67C23A; }

.el-date-editor .el-range-separator {
  width: auto; }

.inner-page-container {
  height: 100%; }

.inner-header {
  padding: 0; }

.inner-main-container {
  padding: 0;
  overflow: hidden; }
  .inner-main-container > > > .el-tabs__content {
    position: absolute;
    top: 0;
    left: 0;
    padding: 50px 0 0; }

.inner-main-content {
  width: 100%;
  height: 100%; }

.inner-content {
  width: 100%;
  height: 100%;
  padding: 0;
  background: #ffffff; }

.content-tools {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px; }
  .content-tools .el-form--inline .el-form-item {
    margin-bottom: 0px; }
  .content-tools.is-flex-end {
    justify-content: flex-end; }
  .content-tools .el-button {
    border-radius: 0;
    padding: 10px 15px; }
  .content-tools .el-icon-upload2,
  .content-tools .el-icon-download {
    width: 14px;
    height: 14px;
    vertical-align: top;
    background-image: url(/static/images/icon-export.png);
    background-repeat: no-repeat;
    background-size: 100% 100%; }
    .content-tools .el-icon-upload2::before,
    .content-tools .el-icon-download::before {
      content: none; }
  .content-tools .el-icon-upload2 {
    background-image: url(/static/images/icon-import.png); }
  .content-tools .el-icon-download {
    background-image: url(/static/images/icon-export.png); }

.tools-right {
  display: flex; }
  .tools-right .el-button + .el-button,
  .tools-right .el-button + .tools-item,
  .tools-right .tools-item + .tools-item,
  .tools-right .tools-item + .el-button {
    margin-left: 20px; }

.container-box {
  padding: 20px 30px; }

.custom-table-header {
  background: #f5f6fa;
  border-bottom: 1px solid #eeeeee;
  font-size: 20px;
  padding: 20px 10px;
  color: #333333;
  text-align: center; }

.custom-table {
  width: 816px;
  margin: 0px auto;
  border: solid #eeeeee;
  border-width: 1px 1px 0 1px; }

.custom-tbody.is-flex {
  display: flex;
  width: 100%; }
  .custom-tbody.is-flex .custom-tr + .custom-tr {
    border-left: 1px solid #eeeeee; }

.custom-tbody.is-inline .custom-th-label {
  min-width: 220px; }

.custom-tbody.is-inline .custom-td-value {
  padding-left: 30px;
  width: calc( 100% - 220px); }

.custom-tbody.is-inline .custom-tr {
  overflow: hidden; }

.custom-tr {
  font-size: 0;
  border-bottom: 1px solid #eeeeee;
  flex: 1;
  background: #fafafa; }
  .custom-tr.is-flex {
    display: flex;
    align-items: center; }
    .custom-tr.is-flex .custom-th-label,
    .custom-tr.is-flex .custom-td-value {
      flex: 1; }
    .custom-tr.is-flex .custom-th-label.is-col-3 {
      flex: 4.01; }
    .custom-tr.is-flex .custom-th-label + .custom-th-label {
      border-left: 1px solid #eeeeee; }

.custom-th-label {
  display: inline-block;
  font-size: 16px;
  color: #333333;
  background: #fafafa;
  text-align: center;
  vertical-align: top;
  padding: 10px 5px;
  height: 100%; }
  .custom-th-label + .custom-td-value {
    border-left: 1px solid #eeeeee; }
  .custom-th-label.is-block {
    display: flex;
    width: 100%;
    flex-wrap: wrap; }
  .custom-th-label.is-flex {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center; }
  .custom-th-label.is-colspan {
    padding: 0; }

.custom-th-label-block {
  display: block;
  width: 100%;
  min-height: 40px;
  line-height: 40px; }
  .custom-th-label-block + .custom-th-label-block {
    border-top: 1px solid #eeeeee; }

.custom-th-label-flex {
  display: flex;
  align-items: center;
  justify-content: center; }

.custom-td-value {
  display: inline-block;
  font-size: 16px;
  color: #333333;
  vertical-align: top;
  padding: 10px 5px;
  min-height: 40px;
  width: 100%;
  background: #ffffff; }
  .custom-td-value + .custom-td-value {
    border-left: 1px solid #eeeeee; }
  .custom-td-value.is-ellipsis {
    width: calc( 100% - 220px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .custom-td-value .el-input.is-disabled .el-input__inner {
    background-color: transparent; }
  .custom-td-value .el-input__inner {
    border: 0;
    height: 18px;
    line-height: 18px; }

.custom-td-text {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%; }

.custom-td-img-list {
  margin: 20px 0 10px;
  font-size: 0; }

.img-list-item {
  display: inline-block;
  border: solid #eeeeee;
  border-width: 4px 0 4px 0px; }
  .img-list-item:first-child {
    border-left-width: 4px; }
  .img-list-item:last-child {
    border-right-width: 4px; }
  .img-list-item + .img-list-item {
    border-left-width: 4px; }
  .img-list-item .item-img {
    display: inline-block;
    width: 90px;
    height: 130px; }

.card-form {
  position: relative; }

.card-button {
  position: absolute;
  right: 0px;
  bottom: 2px; }

.el-tabs--border-card {
  position: relative;
  border-color: #eeeeee;
  height: 100%; }
  .el-tabs--border-card .el-tabs__nav {
    height: 50px; }
  .el-tabs--border-card .el-tabs__item {
    height: 51px;
    line-height: 50px;
    font-size: 16px;
    color: #777777; }
    .el-tabs--border-card .el-tabs__item .badge {
      margin-left: 12px; }
    .el-tabs--border-card .el-tabs__item.is-active {
      color: #409eff;
      border-top: 2px solid #409eff; }
  .el-tabs--border-card .el-tabs__content {
    width: 100%;
    height: 100%; }
    .el-tabs--border-card .el-tabs__content .el-tab-pane {
      height: 100%; }

.el-tag.tag-low {
  background: #4680ff;
  border-color: #4680ff; }

.el-tag.tag-danger {
  background: #d13a38;
  border-color: #d13a38; }

.el-tag.tag-normal {
  background: #fffb09;
  border-color: #fffb09; }

.el-tag.tag-warning {
  background: #ff9309;
  border-color: #ff9309; }

.el-pagination__wrap {
  display: flex;
  justify-content: flex-end;
  margin-top: 15px; }
